import { Component, h } from 'preact';
import { InputString } from './string';
import { SelectMultiple } from './select-multiple';
import { flexCol, flex } from '../constants';
import { isString } from '@ch1/utility';
import { Button } from './button';
import { isMobile } from '../utility';
export class SelectMultipleFilter extends Component {
    constructor() {
        super();
        this.state = { filter: '', options: [] };
    }
    getOptions(props, filter) {
        if (filter) {
            return props.options.filter(option => {
                if (isString(option)) {
                    if (option.toLowerCase().indexOf(filter.toLowerCase()) > -1) {
                        return true;
                    }
                    return false;
                }
                else {
                    if (option.name.toLowerCase().indexOf(filter.toLowerCase()) > -1) {
                        return true;
                    }
                    return false;
                }
            });
        }
        else {
            return this.props.options;
        }
    }
    updateFilter(filter) {
        this.setState(Object.assign(Object.assign({}, this.state), { filter, options: this.getOptions(this.props, filter) }));
    }
    clearFilter() {
        this.setState(Object.assign(Object.assign({}, this.state), { filter: '', options: this.props.options }));
        this.props.onClear();
    }
    componentDidMount() {
        this.setState(Object.assign(Object.assign({}, this.state), { options: this.getOptions(this.props, this.state.filter) }));
    }
    componentWillReceiveProps(props) {
        this.setState(Object.assign(Object.assign({}, this.state), { options: this.getOptions(props, this.state.filter) }));
    }
    render() {
        const givenClasses = this.props.classes ? this.props.classes.join(' ') : '';
        const className = givenClasses.length
            ? `${givenClasses} ${flexCol}`
            : flexCol;
        const onChange = (selected) => {
            this.props.onChange(selected);
        };
        const onDeselect = (index) => {
            this.props.onDeselect(index);
        };
        return (h("div", { className: className },
            h("div", { className: flex },
                isMobile() ? ('') : (h(InputString, { listenKeyUp: true, onChange: this.updateFilter.bind(this), placeholder: "filter", value: this.state.filter })),
                h(Button, { label: "\u2717", onClick: this.clearFilter.bind(this) })),
            isMobile() ? (h(SelectMultiple, { onChange: onChange, options: this.state.options, selected: this.props.selected })) : (h(SelectMultiple, { onChange: onChange, onClick: onDeselect, options: this.state.options, selected: this.props.selected }))));
    }
}
